.scroll {
  overflow-y: scroll;
  height: 65vh;
  margin: 0 20px;
}
.scroll::-webkit-scrollbar {
  width: 10px;
}

.scroll::-webkit-scrollbar-track {
  background: transparent;
}

.scroll::-webkit-scrollbar-thumb {
  background: white;
  opacity: 0.7;
  width: 8px;
  border-radius: 4px;
}
