@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Luckiest+Guy&family=Monoton&display=swap");

body {
  background: linear-gradient(to left, rgb(37, 78, 112), rgb(142, 227, 239));
  font-family: "Luckiest Guy", cursive;
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.header {
  text-align: center;
  margin-bottom: 20px;
}

#title {
  font-family: "Monoton", cursive;
  font-size: 5rem;
  color: #c33c54;
}

.Loading-box {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.Loading {
  margin: auto;
  font-size: 5rem;
}

@media screen and (max-width: 768px) {
  #title {
    font-size: 4rem;
  }
}
